                <li @click="changeTab('tb_setting')" v-bind:class="{active:tab==='tb_setting'}"><h4>组件配置</h4></li>
                <li @click="changeTab('tb_background')" v-bind:class="{active:tab==='tb_background'}"><h4>其他设置</h4></li>
