<script>
    import draggable from "vuedraggable";
    import {eventBus} from "../assets/js/eventBus";
    function getQueryVariable(variable)
    {
           var query = window.location.search.substring(1);
           var vars = query.split("&");
           for (var i=0;i<vars.length;i++) {
                   var pair = vars[i].split("=");
                   if(pair[0] == variable){return pair[1];}
           }
           return "";
    }

    var boederEx="";

    export default {
        name: "Qa",
        display: "Qa",
        order: 2,
        components: {
            "draggable": draggable,
            "tb_component": () => import("../components/ComponentsList.vue"),
            "tb_setting": () => import("../components/ComponentsSetting.vue"),
            "tb_background": () => import("../components/ComponentsBackground.vue")
        },
        data() {

            return {
                list2: mydata,
                tab: 'tb_component',
                currentElement: {},
                activeIndex: -1,
                askName:decodeURI(getQueryVariable("myName")),
                askNameSub:decodeURI(getQueryVariable("myNameSub")),
                ispublic:decodeURI(getQueryVariable("ispublic")),
                isreward:decodeURI(getQueryVariable("isreward")),

                backgroundstyle:"",
                borderstyle:"",//题目边框
                fontStyle:"",//标题样式儿
                littleStyle:"",//副标题样式儿
                fontStyle_com:"",//各题目样式儿
                littleStyle_com:"",//各题目备注样式儿

                //向其他设置里需要传递的值
                backgroundList:backgroundList,
                mtop:mtop,//上边框
                mbottom:mbottom,//下边框
                mleft:mleft,//左边框
                mright:mright,//右边框
                fontFamily:fontFamily,//标题字体
                fontSize:fontSize,//标题字号
                fontLocation:fontLocation,//标题位置
                fontWeight:fontWeight,//标题是否加粗
                fontFamily_com:fontFamily_com,//各题目字体
                fontSize_com:fontSize_com,//各题目字号
                fontLocation_com:fontLocation_com,//各题目位置
                fontWeight_com:fontWeight_com//各题目是否加粗
            };

        },
        created() {

            eventBus.$on("pickup",v=>{//背景图片设置
                this.backgroundstyle="";
                this.backgroundstyle=v.backstyle;
            }),
            eventBus.$on("changeTop", ({obj1, obj2}) => {
                if(this.borderstyle==""){
                    this.borderstyle="border-top:"+obj1+"px solid "+obj2+";";
                }else{
                    this.borderstyle=this.borderstyle+"border-top:"+obj1+"px solid "+obj2+";";
                }
            }),
            eventBus.$on("changeBottom",({obj1, obj2})=>{//题目下边框设置
                if(this.borderstyle==""){
                    this.borderstyle="border-bottom:"+obj1+"px solid "+obj2+";";
                }else{
                    this.borderstyle=this.borderstyle+"border-bottom:"+obj1+"px solid "+obj2+";";
                }
            }),
            eventBus.$on("changeLeft",({obj1, obj2})=>{//题目左边框设置
                if(this.borderstyle==""){
                    this.borderstyle="border-left:"+obj1+"px solid "+obj2+";";
                }else{
                    this.borderstyle=this.borderstyle+"border-left:"+obj1+"px solid "+obj2+";";
                }
            }),
            eventBus.$on("changeRight",({obj1, obj2})=>{//题目右边框设置
                if(this.borderstyle==""){
                    this.borderstyle="border-right:"+obj1+"px solid "+obj2+";";
                }else{
                    this.borderstyle=this.borderstyle+"border-right:"+obj1+"px solid "+obj2+";";
                }
            }),
            eventBus.$on("changeTitleFont",v=>{//标题字体设置
                if(this.fontStyle==""){
                    this.fontStyle="font-family:"+v+";";
                }else{
                    this.fontStyle=this.fontStyle+"font-family:"+v+";";
                }
            }),
            eventBus.$on("changeTitleFont_com",v=>{//各题目字体设置
                if(this.fontStyle_com==""){
                    this.fontStyle_com="font-family:"+v+";";
                }else{
                    this.fontStyle_com=this.fontStyle_com+"font-family:"+v+";";
                }
            }),
            eventBus.$on("changeTitleLocation",v=>{//标题位置设置
                if(this.fontStyle==""){
                    this.fontStyle="text-align:"+v+";";
                }else{
                    this.fontStyle=this.fontStyle+"text-align:"+v+";";
                }
                this.littleStyle="text-align:"+v+";";
            }),
            eventBus.$on("changeTitleLocation_com",v=>{//各题目位置设置
                if(this.fontStyle_com==""){
                    this.fontStyle_com="text-align:"+v+";";
                }else{
                    this.fontStyle_com=this.fontStyle_com+"text-align:"+v+";";
                }
                this.littleStyle_com="text-align:"+v+";";
            }),
            eventBus.$on("changeTitleWeight",v=>{//标题加粗设置
                if(this.fontStyle==""){
                    this.fontStyle="font-weight:"+v+";";
                }else{
                    this.fontStyle=this.fontStyle+"font-weight:"+v+";";
                }
            }),
            eventBus.$on("changeTitleWeight_com",v=>{//各题目加粗设置
                if(this.fontStyle_com==""){
                    this.fontStyle_com="font-weight:"+v+";";
                }else{
                    this.fontStyle_com=this.fontStyle_com+"font-weight:"+v+";";
                }
            }),
            eventBus.$on("changeTitleSize",v=>{//标题字号设置
                this.fontSize=v;
                if(this.fontStyle==""){
                    this.fontStyle="font-size:"+v+"px;";
                }else{
                    this.fontStyle=this.fontStyle+"font-size:"+v+"px;";
                }
            }),
            eventBus.$on("changeTitleSize_com",v=>{//各题目字号设置
                this.fontSize_com=v;
                if(this.fontStyle_com==""){
                    this.fontStyle_com="font-size:"+v+"px;";
                }else{
                    this.fontStyle_com=this.fontStyle_com+"font-size:"+v+"px;";
                }
            }),
            eventBus.$on("pickmove",v=>{//单击题型新增题目
                let item = JSON.parse(JSON.stringify(v));
                item.id = this.list2.length;
                this.list2.push(item);
                this.tab = 'tb_component';
                mydata=this.list2;
            })
        },
        mounted(){
            for(var i=0;i<this.backgroundList.length;i++){
                if(this.backgroundList[i].style1=="border:3px solid #16A3FB;"){
                    if(this.backgroundList[i].id1!=0){
                        //this.backgroundstyle="background: url("+this.backgroundList[i].src1+") no-repeat center 0;";
                        //console.log(this.backgroundList[i].id1);
                        this.backgroundstyle="background: url("+this.backgroundList[i].src1+") center center / cover no-repeat fixed;";
                        break;
                    }
                }else if(backgroundList[i].style2=="border:3px solid #16A3FB;"){
                    //this.backgroundstyle="background: url("+this.backgroundList[i].src2+") no-repeat center 0;";
                    //console.log(this.backgroundList[i].id2);
                    this.backgroundstyle="background: url("+this.backgroundList[i].src2+") center center / cover no-repeat fixed;";
                    break;
                }
            }
            this.borderstyle="border-top:"+this.mtop.number+"px solid "+this.mtop.color+";";
            this.borderstyle+="border-bottom:"+this.mbottom.number+"px solid "+this.mbottom.color+";";
            this.borderstyle+="border-left:"+this.mleft.number+"px solid "+this.mleft.color+";";
            this.borderstyle+="border-right:"+this.mright.number+"px solid "+this.mright.color+";";
            for(var i=0;i<this.fontFamily.length;i++){
                if(this.fontFamily[i].pick){
                    this.fontStyle=this.fontStyle+"font-family:"+this.fontFamily[i].name+";";
                    break;
                }
            }
            this.fontStyle=this.fontStyle+"font-size:"+this.fontSize+"px;";
            for(var i=0;i<this.fontLocation.length;i++){
                if(this.fontLocation[i].pick){
                    this.fontStyle+="text-align:"+this.fontLocation[i].inname+";";
                    this.littleStyle="text-align:"+this.fontLocation[i].inname+";";
                    break;
                }
            }
            for(var i=0;i<this.fontWeight.length;i++){
                if(this.fontWeight[i].pick){
                    this.fontStyle+="font-weight:"+this.fontWeight[i].inname+";";
                    break;
                }
            }
            //各标题
            for(var i=0;i<this.fontFamily_com.length;i++){
                if(this.fontFamily_com[i].pick){
                    this.fontStyle_com="font-family:"+this.fontFamily_com[i].name+";";
                    break;
                }
            }
            this.fontStyle_com+="font-size:"+this.fontSize_com+"px;";
            for(var i=0;i<this.fontLocation_com.length;i++){
                if(this.fontLocation_com[i].pick){
                    this.fontStyle_com+="text-align:"+this.fontLocation_com[i].inname+";";
                    this.littleStyle_com="text-align:"+this.fontLocation_com[i].inname+";";
                    break;
                }
            }
            for(var i=0;i<this.fontWeight_com.length;i++){
                if(this.fontWeight_com[i].pick){
                    this.fontStyle_com+="font-weight:"+this.fontWeight_com[i].inname+";";
                    break;
                }
            }
        },
        methods: {
            move: function () {
                // 重新给数据分配ID 很重要
                let tmp = [];
                for (let i = 0; i < this.list2.length; i++) {
                    let item = JSON.parse(JSON.stringify(this.list2[i]));
                    item.id = i;
                    tmp.push(item);
                }
                this.list2 = tmp;
                this.tab = 'tb_component';
                mydata=this.list2;

            },
            edit: function (idx) {
                this.currentElement = this.list2[idx];
                eventBus.$emit('dataChange', this.currentElement);
                this.tab = 'tb_setting';
                this.activeIndex = idx;
            },
            showhide:function(idx){
                //this.currentElement = this.list2[idx];
                //console.log(this.currentElement.id);
            },
            handleChange: function () {
            },
            deleteItem: function (index) {
                const temp = this.list2;
                temp.splice(index, 1);
                this.$set(this.list2,temp);
                let tmp = [];
                for (let i = 0; i < this.list2.length; i++) {
                    let item = JSON.parse(JSON.stringify(this.list2[i]));
                    item.id = i;
                    tmp.push(item);
                }
                this.list2 = tmp;
                this.tab = 'tb_component';
                mydata=this.list2;
            },
            changeTab: function (id) {
                this.tab = id;
                if(id=="tb_background"){
                    //带回背景图片以及其他设置
                    this.currentElement={
                        backgroundList:this.backgroundList,
                        mtop:this.mtop,
                        mbottom:this.mbottom,
                        mleft:this.mleft,
                        mright:this.mright,
                        fontFamily:this.fontFamily,
                        fontSize:this.fontSize,
                        fontLocation:this.fontLocation,
                        fontWeight:this.fontWeight,
                        fontFamily_com:this.fontFamily_com,
                        fontSize_com:this.fontSize_com,
                        fontLocation_com:this.fontLocation_com,
                        fontWeight_com:this.fontWeight_com}
                    eventBus.$emit('dataReturn', this.currentElement);

                }
            },
            preview: function () {
                //this.setPageDataToStorage();
                window.open('/lnnsgl/lnkhzs/survey/addTiku.jsp');
            },
            setPageDataToStorage: function () {
                sessionStorage.setItem('pageData', JSON.stringify(this.list2));
            },
            save: function () {
                var a = JSON.stringify(this.list2);//各题目数据
                var backgroundList=JSON.stringify(this.backgroundList);//背景详情
                var mtop=JSON.stringify(this.mtop);//各题目上边框大小以及颜色设置
                var mbottom=JSON.stringify(this.mbottom);//各题目下边框大小以及颜色设置
                var mleft=JSON.stringify(this.mleft);//各题目左边框大小以及颜色设置
                var mright=JSON.stringify(this.mright);//各题目右边框大小以及颜色设置
                var fontFamily=JSON.stringify(this.fontFamily);//标题字体
                var fontSize=this.fontSize;//标题字号
                var fontLocation=JSON.stringify(this.fontLocation);//标题位置
                var fontWeight=JSON.stringify(this.fontWeight);//标题加粗
                var fontFamily_com=JSON.stringify(this.fontFamily_com);//各题目字体
                var fontSize_com=this.fontSize_com;//各题目字号
                var fontLocation_com=JSON.stringify(this.fontLocation_com);//各题目位置
                var fontWeight_com=JSON.stringify(this.fontWeight_com);//各题目加粗
                var isgo=true;
                for(var i=0;i<this.list2.length;i++){
                    if(this.list2[i].comp=="danxuan"||this.list2[i].comp=="duoxuan"){
                        for(var j=0;j<this.list2[i].hideitems.length;j++){
                            var optitem=this.list2[i].hideitems[j].optitem;
                            if(optitem>this.list2[i].options.length){
                                alert("不存在该选项号,请确认第"+(i+1)+"题的选项号");
                                isgo=false;
                                break;
                            }
                            if(optitem==""){
                                alert("选项号为空,请确认第"+(i+1)+"题的选项号");
                                isgo=false;
                                break;
                            }
                        }
                        for(var j=0;j<this.list2[i].hidenum.length;j++){
                            var optitem=this.list2[i].hidenum[j].optitem;
                            if(optitem>this.list2.length){
                                alert("不存在该题号,请确认第"+(i+1)+"题的隐藏题号");
                                isgo=false;
                                break;
                            }
                            if(optitem<=(i+1)){
                                alert("隐藏题号必须大于当前题目,请确认第"+(i+1)+"题的隐藏题号");
                                isgo=false;
                                break;
                            }
                            if(optitem==""){
                                alert("隐藏题号为空,请确认第"+(i+1)+"题的隐藏题号");
                                isgo=false;
                                break;
                            }
                            if(optitem.indexOf(".")!=-1){
                                alert("当前题号不能为小数,请确认第"+(i+1)+"题的隐藏题号");
                                isgo=false;
                                break;
                            }
                        }
                    }
                }
                //console.log(a);
                //console.log(isgo);
                if(this.askName==""){
                    alert("请输入问卷名称");
                    return ;
                }
                if(isgo){
                    $.ajax({
                        url:"/lnfwzl/khzs/vue/surveys/doAction.action",
                        type:"POST",
                        dataType:"jsonp",
                        data:{
                            "myquestion": "\""+a+"\"",
                            "myAsk":getQueryVariable("myAsk"),
                            "myName":this.askName,
                            "myNameSub":this.askNameSub,
                            "ispublic":$("#showSelected").html(),
                            "isreward":$("#showRewardSelected").html(),
                            "backgroundstyle":this.backgroundstyle,
                            "borderstyle":this.borderstyle,//题目边框
                            "fontStyle":this.fontStyle,//标题样式儿
                            "fontStyle_com":this.fontStyle_com,//各题目样式儿
                            "backgroundList":backgroundList,
                            "mtop":mtop,
                            "mbottom":mbottom,
                            "mright":mright,
                            "mleft":mleft,
                            "fontFamily":fontFamily,
                            "fontSize":fontSize,
                            "fontLocation":fontLocation,
                            "fontWeight":fontWeight,
                            "fontFamily_com":fontFamily_com,
                            "fontSize_com":fontSize_com,
                            "fontLocation_com":fontLocation_com,
                            "fontWeight_com":fontWeight_com
                        },
                        success:function(data){
                            alert("保存成功");
                            window.opener.abc();
                            window.close();
                        },
                        error:function(data){
                            alert("保存成功");
                            window.opener.abc();
                            window.close();
                        }
                    })
                }

            },
            selectChange:function(obj){
                if(obj==1){
                    $("#showSelected").html("公有");
                }else{
                    $("#showSelected").html("私有");
                }
            },
            selectReward:function(obj){
                if(obj==1){
                    $("#showRewardSelected").html("有奖励");
                }else{
                    $("#showRewardSelected").html("无奖励");
                }
            },
            execldemo:function(){
                window.open('/lnfwzl/lnkhzs/survey/dist/问卷导入.xlsx');
            }
        }
    };


</script>

